button {
  margin:0 2px;
  &.prev {
    border-radius: 2px;
    //padding: 0.8em 2.5em;
    border: none;
    font-size: 0.9rem;
    background-color:#ececec;
    color: #818181;
    width:140px;
    height:40px;    

    &:hover {
      background-color: #8888;
    }
  }

  &.next {
    border-radius: 2px;
    //padding: 0.8em 2.5em;
    border: none;
    font-size: 0.9rem;
    background-color: #30a4ff;
    color: #fff;
    width:140px;
    height:40px;

    &:hover {
      background-color: #0087f1;
    }
  }
}
