nav{
    position: absolute;
    top: 0;
    left: 0;
    width: 256px;
    height: 100%;
    background-color: #fafafa;

    .nav__wrapper{
        .nav__wrapper__active{
            margin-top:2em;
        }

        .nav__wrapper__inactive{
            .nav__wrapper__inactive--title{
                height: 28px;
                line-height: 28px;
                margin: 0 1.2em;
                strong{
                    font-size: 0.7rem;
                    color:#ccc;                
                    
                }
            }            

            ul{
                li{
                    span{
                        color:#9f9f9f;
                    }
                }
            }
        }

        ul{
            li{      
                &:hover{
                    background:#f1f1f1;
                }      
                height: 48px;    
                line-height: 48px;
                padding: 0 2em;   
                font-size: 0.85rem;     
                cursor: pointer;
                img{
                    width: 15px;
                    height: 15px;
                    object-fit: contain;
                    vertical-align: middle;
                    margin-right:0.5em;
                }
                span{
                    color: #4c4c4c;
                }
                i{
                    color:#ccc;      
                    margin-left:0.25em;      
                }
            }
        }
    }
}

@media all and (max-width: 1366px){
    nav{
        width: 64px;   

        .nav__wrapper{
            ul{
                li{            
                    height: 48px;    
                    line-height: 48px;
                    padding: 0;   
                    text-align: center;                    
                    span{
                        display: none;
                    }
                    i{
                        color:#ccc;                                    
                        margin-left: 0;
                    }
                }
            }
            .nav__wrapper__active{
                ul{
                    li{
                        img{
                            display: none;
                        }
                    }
                }
            }     
            .nav__wrapper__inactive{                
                .nav__wrapper__inactive--title{
                    text-align: center;
                    i{
                        width: 8px;
                        height: 8px;
                        background: #e8e8e8;
                        border-radius: 50%;
                        display: inline-block;
                        text-align: center;
                    }
                    strong{
                        display: none;
                    }
                }                
                ul{
                    li{
                        span{
                            display: none;
                        }
                        img{
                            margin-right: 0;
                        }
                    }
                }
            }       
        }
    }    
}